$font-article: Montserrat, sans-serif;
$font-basic: Muli, sans-serif;
$font-lato: Lato, sans-serif;
$font-poppins: Poppins, sans-serif;
$background-color_menu: #323232;
$red_in_site: #e20505;
$h6_color: #9c9c9c;
$menu_single_elements_hover_color: #494949;
$green: #59ec0f;
$light_grey: #d6d6d6;
$orange: #ff7400;

/*  General Style   */

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin-block-start: 0px;
  margin-block-end: 0px;
}
h6 {
  font: 16px $font-lato;
}

p {
  font: 16px $font-basic;
}

body {
  margin: 0px;
  background-color: #fafbfc;
}
